import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Link } from "react-router-dom";

import { useLocale } from "../../hooks";
import RegionsSliderTablist from "../regionsSliderTablist/RegionsSliderTablist";
import CardRegionSlide from "../cardRegion/CardRegion";

import "swiper/css";
import "./regions.scss";

const tablist = { uk: ["", "", ""], en: ["Kyiv", "Odesa", "Kharkiv"] };

const Regions = () => {
	const { t, language } = useLocale();
	const [selectedTab, setSelectedTab] = React.useState(tablist[language][0]);

	React.useEffect(() => setSelectedTab(tablist[language][0]), [language]);

	return (
		<div className="regions">
			<div className="regions__header">
				<h2 className="regions__title title">{t("regions__title")}</h2>
				<Link to="/anypage" className="regions__link" aria-label="     ">
					{t("regions__link")}
				</Link>
			</div>
			<div className="regions__body">
				<Swiper
					className="regions__slider regions-slider"
					wrapperTag="ul"
					spaceBetween={16}
					speed={1000}
					watchSlidesProgress
					slidesPerView={"auto"}
					slidesOffsetBefore={24}
					slidesOffsetAfter={24}
					allowTouchMove={false}
					breakpoints={{
						992: {
							slidesOffsetBefore: 0,
							slidesOffsetAfter: 0,
						},
						1400: {
							allowTouchMove: true,
							slidesOffsetBefore: 0,
							slidesOffsetAfter: 0,
						},
					}}
				>
					<div slot="container-start">
						<RegionsSliderTablist
							className="regions__tablist"
							tablist={tablist[language]}
							selectedTab={selectedTab}
							changeSelectedTab={setSelectedTab}
						/>
					</div>
					{tablist[language].map((item) => (
						<SwiperSlide key={item} className="regions-slider__slide" tag="li">
							<CardRegionSlide city={item} />
						</SwiperSlide>
					))}
				</Swiper>
			</div>
		</div>
	);
};

export default Regions;
