import React from "react";

import "./loadingFallback.scss";

const LoadingFallback = () => {
	return (
		<div className="loading-fallback" aria-hidden>
			<div className="loading-fallback__inner">
				<div className="loading-fallback__circle"></div>
				<div className="loading-fallback__circle"></div>
				<div className="loading-fallback__circle"></div>
				<div className="loading-fallback__shadow"></div>
				<div className="loading-fallback__shadow"></div>
				<div className="loading-fallback__shadow"></div>
				<span className="loading-fallback__text">Loading</span>
			</div>
		</div>
	);
};

export default LoadingFallback;
