import React from "react";
import { Link } from "react-router-dom";

import { useLocale } from "../../hooks";

import "./anyPage.scss";
import image from "../../assets/images/any-page/maintenance.gif";

const AnyPage = () => {
	const { t } = useLocale();
	return (
		<div className="any-page">
			<div className="container">
				<img
					className="any-page__image"
					src={image}
					alt="         "
				/>
				<p className="any-page__title">
					{t("any-page__title-part-1")} <span>{t("any-page__title-part-2")}</span>{" "}
					{t("any-page__title-part-3")}!
				</p>
				<p className="any-page__subtitle">
					{t("any-page__subtitle-row-1")}
					<br />
					{t("any-page__subtitle-row-2")}
				</p>
				<Link to="/" className="any-page__link" aria-label="  " replace>
					{t("any-page__link-back")}
				</Link>
			</div>
		</div>
	);
};

export default AnyPage;
