import { t } from "i18next";
import React from "react";
import { Link } from "react-router-dom";

import MenuItemAccordion from "../menuItemAccordion/MenuItemAccordion";

import "./menu.scss";

const Menu = ({ toggleMenu }) => {
	return (
		<div className="menu">
			<div className="menu__top top-menu">
				<ul className="top-menu__list">
					<li className="top-menu__item">
						<Link
							className="top-menu__link"
							to="/anypage"
							aria-label="   -  "
							onClick={toggleMenu}
						>
							{t("menu-title__all-news")}
						</Link>
					</li>
					<li className="top-menu__item">
						<Link
							className="top-menu__link"
							to="/anypage"
							aria-label="   - COVID-19"
							onClick={toggleMenu}
						>
							{t("menu-title__COVID-19")}
						</Link>
					</li>
					<li className="top-menu__item">
						<Link
							className="top-menu__link"
							to="/anypage"
							aria-label="   - "
							onClick={toggleMenu}
						>
							{t("menu-title__war")}
						</Link>
					</li>
				</ul>
			</div>
			<div className="menu__middle middle-menu">
				<MenuItemAccordion
					title={t("menu-title__politics")}
					links={[
						{ label: t("menu-item__Ukraine"), url: "#" },
						{ label: t("menu-item__world"), url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title={t("menu-title__health")}
					links={[
						{ label: t("menu-item__diets"), url: "#" },
						{ label: t("menu-item__beauty"), url: "#" },
						{ label: t("menu-item__sex"), url: "#" },
						{ label: t("menu-item__psychology"), url: "#" },
						{ label: t("menu-item__medicine"), url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title={t("menu-title__events")}
					links={[
						{ label: t("menu-item__road-accidents"), url: "#" },
						{ label: t("menu-item__disasters"), url: "#" },
						{ label: t("menu-item__crime"), url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title={t("menu-title__lifestyle")}
					links={[
						{ label: t("menu-item__life-stories"), url: "#" },
						{ label: t("menu-item__travels"), url: "#" },
						{ label: t("menu-item__cuisine"), url: "#" },
						{ label: t("menu-item__show-business"), url: "#" },
						{ label: t("menu-item__fashion"), url: "#" },
						{ label: t("menu-item__horoscope"), url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title={t("menu-title__society")}
					links={[
						{ label: t("menu-item__health-care"), url: "#" },
						{ label: t("menu-item__safety"), url: "#" },
						{ label: t("menu-item__ecology"), url: "#" },
						{ label: t("menu-item__religion"), url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title={t("menu-title__regions")}
					links={[
						{ label: t("city-Kyiv"), url: "#" },
						{ label: t("city-Odesa"), url: "#" },
						{ label: t("city-Kharkiv"), url: "#" },
						{ label: t("city-Lviv"), url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title={t("menu-title__technologies")}
					links={[
						{ label: t("menu-item__science"), url: "#" },
						{ label: t("menu-item__gadgets"), url: "#" },
						{ label: t("menu-item__it"), url: "#" },
						{ label: t("menu-item__universe"), url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
			</div>
		</div>
	);
};

export default Menu;
