import { t } from "i18next";
import React from "react";
import { Link } from "react-router-dom";

import { useLocale } from "../../hooks";
import { getFormattedTime } from "../../services";
import MoreButton from "../moreButton/MoreButton";

import "./cardRegion.scss";

const CardRegion = ({ city }) => {
	const { t, language } = useLocale();

	const dateTime1 = "2022-08-05T14:59";
	const date1 = getFormattedTime(dateTime1, language, "date");
	const time1 = getFormattedTime(dateTime1, language, "time");

	return (
		<div className="card-region">
			<h3 className="card-region__title">{city}</h3>
			<div className="card-region__content">
				<div className="card-region__news">
					<div className="card-region__day">
						<time className="card-region__news-date" dateTime={dateTime1}>
							{date1}
						</time>
						<ul className="card-region__list">
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">{time1}</time>
									{t("block-news__news-1")}
								</Link>
							</li>
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">{time1}</time>
									{t("block-news__news-2")}
								</Link>
							</li>
						</ul>
					</div>
					<div className="card-region__day">
						<time className="card-region__news-date" dateTime="2022-08-05">
							{date1}
						</time>
						<ul className="card-region__list">
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">{time1}</time>
									{t("block-news__news-3")}
								</Link>
							</li>
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">{time1}</time>
									{t("block-news__news-4")}
								</Link>
							</li>
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">{time1}</time>
									{t("block-news__news-5")}
								</Link>
							</li>
						</ul>
					</div>
				</div>
			</div>
			<MoreButton className={"card-region__more"} label={t("btn-more-news")} url={"#"} />
		</div>
	);
};

export default CardRegion;
