import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { useTranslation } from "react-i18next";

import "swiper/css";
import "./bottomHeaderSlider.scss";
import icon01 from "../../assets/images/header/icons/losses-slider/01.svg";
import icon02 from "../../assets/images/header/icons/losses-slider/02.svg";
import icon03 from "../../assets/images/header/icons/losses-slider/03.svg";
import icon04 from "../../assets/images/header/icons/losses-slider/04.svg";
import icon05 from "../../assets/images/header/icons/losses-slider/05.svg";
import icon06 from "../../assets/images/header/icons/losses-slider/06.svg";
import icon07 from "../../assets/images/header/icons/losses-slider/07.svg";

const BottomHeaderSlider = () => {
	const { t } = useTranslation();
	return (
		<div className="bottom-header">
			<p className="bottom-header__label">131 {t("bottom-header-item__day-of-war")}:</p>
			<Swiper
				className="losses-slider"
				slidesPerView="auto"
				slidesPerGroup={1}
				spaceBetween={40}
				speed={1000}
				slidesOffsetAfter={24}
				slidesOffsetBefore={24}
			>
				<SwiperSlide className="losses-slider__slide losses-slide">
					<img
						className="losses-slide__image"
						src={icon01}
						alt=" -  "
						aria-hidden="true"
					/>
					<div className="losses-slide__title">{t("bottom-header-item__personnel")}</div>
					<div className="losses-slide__value">~36 200</div>
				</SwiperSlide>
				<SwiperSlide className="losses-slider__slide losses-slide">
					<img
						className="losses-slide__image"
						src={icon02}
						alt=" -  "
						aria-hidden="true"
					/>
					<div className="losses-slide__title">{t("bottom-header-item__tanks")}</div>
					<div className="losses-slide__value">1 589</div>
				</SwiperSlide>
				<SwiperSlide className="losses-slider__slide losses-slide">
					<img
						className="losses-slide__image"
						src={icon03}
						alt=" -  "
						aria-hidden="true"
					/>
					<div className="losses-slide__title">{t("bottom-header-item__artillery-systems")}</div>
					<div className="losses-slide__value">4578</div>
				</SwiperSlide>
				<SwiperSlide className="losses-slider__slide losses-slide">
					<img
						className="losses-slide__image"
						src={icon04}
						alt=" -  "
						aria-hidden="true"
					/>
					<div className="losses-slide__title">{t("bottom-header-item__planes")}</div>
					<div className="losses-slide__value">220</div>
				</SwiperSlide>
				<SwiperSlide className="losses-slider__slide losses-slide">
					<img
						className="losses-slide__image"
						src={icon05}
						alt=" -  "
						aria-hidden="true"
					/>
					<div className="losses-slide__title">{t("bottom-header-item__MLRS")}</div>
					<div className="losses-slide__value">246</div>
				</SwiperSlide>
				<SwiperSlide className="losses-slider__slide losses-slide">
					<img
						className="losses-slide__image"
						src={icon06}
						alt=" -  "
						aria-hidden="true"
					/>
					<div className="losses-slide__title">{t("bottom-header-item__helicopters")}</div>
					<div className="losses-slide__value">190</div>
				</SwiperSlide>
				<SwiperSlide className="losses-slider__slide losses-slide">
					<img
						className="losses-slide__image"
						src={icon07}
						alt=" -  "
						aria-hidden="true"
					/>
					<div className="losses-slide__title">{t("bottom-header-item__AFV")}</div>
					<div className="losses-slide__value">4578</div>
				</SwiperSlide>
			</Swiper>
		</div>
	);
};

export default BottomHeaderSlider;
