import React from "react";
import classNames from "classnames";
import { Link } from "react-router-dom";

import { useLocale } from "../../hooks";
import { getFormattedTime } from "../../services/formatDateTime";

import "./blockNews.scss";

const labels = {
	uk: ["  ", " ", "  ", ""],
	en: ["News with a photo", "Urgent news", "News with a video", "Online"],
};

const BlockNews = ({ className, title, titles, label, dateTime, url, type, size }) => {
	const { language } = useLocale();

	React.useEffect(() => {}, []);

	const itemClassnames = classNames({
		[className]: className,
		"block-news": true,
		"block-news--with-photo": label && label === "  ",
		"block-news--hot": label && label === " ",
		"block-news--video": label && label === "  ",
		"block-news--online": label && label === "",
		"block-news--bigger-padding": size && size === "large",
	});

	const getLabelIndex = () => {
		return labels["uk"].findIndex((item) => item === label);
	};

	return (
		<li className={itemClassnames}>
			{label && <p className="block-news__label">{labels[language][getLabelIndex()]}</p>}
			<Link to="/anypage" className="block-news__link">
				<time dateTime={dateTime}>{getFormattedTime(dateTime, language, "time")}</time>
				{titles ? titles[language] : title}
			</Link>
		</li>
	);
};

export default BlockNews;
