import React from "react";
import classNames from "classnames";
import { Link } from "react-router-dom";

import { useLocale } from "../../hooks";
import { getFormattedTime } from "../../services";

import "./blockArticle.scss";

const BlockArticle = ({ titles, imageSrc, imageAlt, url, dateTime, isLarge }) => {
	const { language } = useLocale();
	const titleClassnames = classNames("block-article__title", {
		"block-article__title--large": isLarge,
	});

	return (
		<article className="block-article">
			<Link to="/anypage" className="block-article__image" aria-label="   ">
				<img src={imageSrc} alt={imageAlt} />
			</Link>
			<time className="block-article__time" dateTime={dateTime}>
				{getFormattedTime(dateTime, language)}
			</time>
			<Link to="/anypage" className={titleClassnames} aria-label="   ">
				{titles[language]}
			</Link>
		</article>
	);
};

export default BlockArticle;
