import React from "react";

import { useLocale } from "../../hooks";
import { getFormattedTime } from "../../services";

import "./promoThumbSlide.scss";

const PromoThumbSlide = ({ titles, dateTime }) => {
	const { language } = useLocale();
	return (
		<div className="promo-thumb-slide">
			<time className="promo-thumb-slide__time" dateTime={dateTime}>
				{getFormattedTime(dateTime, language)}
			</time>
			<p className="promo-thumb-slide__title">{titles[language]}</p>
		</div>
	);
};

export default PromoThumbSlide;
