const BlockColumns = () => {
	const { t, language } = useLocale();
	return (
		<div className="block-columns">
			<div className="block-columns__header">
				<p className="block-columns__title">{t("block-columns__title")}</p>
				<Link to="/anypage" className="block-columns__link">
					{t("block-columns__link")}
				</Link>
			</div>
			<div className="block-columns__body">
				{columnsData && columnsData.length && (
					<>
						<ul className="block-columns__list">
							{columnsData.map((item) => (
								<li key={item[language].author} className="block-columns__item">
									<ItemColumn {...item[language]} />
								</li>
							))}
						</ul>
						<Swiper
							className="block-columns__slider block-columns-slider"
							wrapperTag="ul"
							slidesPerView={"auto"}
							slidesPerGroup={1}
							spaceBetween={24}
							speed={1000}
							slidesOffsetBefore={24}
							slidesOffsetAfter={24}
						>
							{columnsData.map((item) => (
								<SwiperSlide
									key={item[language].author}
									className="block-columns-slider__slide"
									tag="li"
								>
									<ItemColumn className="block-columns-slider__slide" {...item[language]} />
								</SwiperSlide>
							))}
						</Swiper>
					</>
				)}
				<LoadMoreButton className="block-columns__more" title={t("block-columns__link")} url="#" withoutIcon />
			</div>
		</div>
	);
};

export default BlockColumns;
