export const getFormattedTime = (dateTimeString, language = "uk", onlyPart = false) => {
	const dateTime = new Date(dateTimeString);

	if (!onlyPart && (!language || language === "uk")) {
		const dateOptions = { day: "2-digit", month: "long", hour: "numeric", minute: "numeric" };
		const time = new Intl.DateTimeFormat("uk", dateOptions).format(dateTime);

		return time.replace(/  /gi, " ");
	}

	if (!onlyPart && language === "en") {
		const dateOptions = { day: "2-digit", month: "short", hour: "numeric", minute: "numeric" };
		const time = new Intl.DateTimeFormat("en", dateOptions).format(dateTime);

		return time;
	}

	if (onlyPart === "time") {
		const dateOptions = { hour: "numeric", minute: "numeric" };
		const time = new Intl.DateTimeFormat(language, dateOptions).format(dateTime);

		return time;
	}
	if (onlyPart === "date") {
		const dateOptions = { day: "numeric", month: "long" };
		const time = new Intl.DateTimeFormat(language, dateOptions).format(dateTime);

		return time;
	}
	if (onlyPart === "dateTimeShort") {
		const dateOptions = { hour: "numeric", minute: "numeric", day: "2-digit", month: "short" };
		const time = new Intl.DateTimeFormat(language, dateOptions).format(dateTime);

		return time;
	}
};
