import React from "react";
import { Routes, Route } from "react-router-dom";

import { useScrollLock } from "../../hooks";
import { scrollLockContext } from "../../context";
import ScrollToTop from "../scrollToTop/ScrollToTop";
import Header from "../header/Header";
import { HomePage, AnyPage } from "../../pages";
import Footer from "../footer/Footer";

const App = () => {
	const { isScrollLocked, lockScroll, unlockScroll, toggleScrollLock, scrollbarWidth } = useScrollLock();

	return (
		<scrollLockContext.Provider
			value={{ isScrollLocked, lockScroll, unlockScroll, toggleScrollLock, scrollbarWidth }}
		>
			<ScrollToTop />
			<Header />
			<div className="wrapper">
				<Routes>
					<Route path="/" element={<HomePage />} />
					<Route path="/anypage" element={<AnyPage />} />
				</Routes>
			</div>
			<Footer />
		</scrollLockContext.Provider>
	);
};

export default App;
