import {
    useTranslation
} from 'react-i18next';

const useLocale = () => {
    const {
        t,
        i18n
    } = useTranslation();

    const {
        language,
        changeLanguage
    } = i18n;

    return {
        t,
        language,
        changeLanguage,
    }
}

export default useLocale;