import React from "react";
import { Link } from "react-router-dom";
import { FocusTrap } from "@mui/base";
import { CSSTransition } from "react-transition-group";

import { scrollLockContext } from "../../context";
import { useLocale, useDebounce, useMockAPI, useScrollbar, useMatchMedia } from "../../hooks";
import { getFormattedTime } from "../../services";

import "./topHeaderSearch.scss";

const TopHeaderSearch = () => {
	const { t, language } = useLocale();
	const { isMobile } = useMatchMedia();
	const [isFormFocused, setFormFocused] = React.useState(false);
	const [isResultVisible, setResultVisible] = React.useState(false);
	const [isFocusedOnResults, setFocusedOnResults] = React.useState(false);
	const [searchString, setSearchString] = React.useState("");
	const searchStringDebounced = useDebounce(searchString, 500);
	const [news, setNews] = React.useState(null);
	const { getSearchResults } = useMockAPI();
	const { lockScroll, unlockScroll, toggleScrollLock } = React.useContext(scrollLockContext);

	const topHeaderSearchRef = React.useRef(null);
	const searchResultsRef = React.useRef(null);

	useScrollbar(searchResultsRef, news && news.length && !isMobile);

	React.useEffect(() => {
		searchNews();
	}, [searchStringDebounced]);

	React.useEffect(() => {
		if (isResultVisible) {
			window.addEventListener("keydown", handleWindowKeydown);
			window.addEventListener("click", handleClickOutsideComponent);

			return () => {
				window.removeEventListener("keydown", handleWindowKeydown);
				window.removeEventListener("click", handleClickOutsideComponent);
			};
		}
	}, [isResultVisible]);

	// Clearing search input & news if language changes
	React.useEffect(() => {
		setSearchString("");
		setNews(null);
	}, [language]);

	// Handling mobile phone orientation change while search-mobile is opened
	React.useEffect(() => {
		if (isResultVisible && !isMobile) {
			hideSearchResults();
			setNews(null);
		}
	}, [isMobile]);

	const toggleFormFocus = () => {
		setFormFocused((state) => !state);
	};

	const hideSearchResults = () => {
		setResultVisible(false);
		setFocusedOnResults(false);
		unlockScroll();
	};

	const searchNews = async () => {
		if (searchStringDebounced.length > 2) {
			await getSearchResults(searchStringDebounced, language, 1, 10)
				.then(setNews)
				.then(() => {
					setResultVisible(true);
					lockScroll();
				});
		} else {
			setNews(null);
		}
	};

	const handleInputChange = (event) => {
		event.preventDefault();
		setSearchString(event.target.value);
	};

	const handleInputKeydown = (event) => {
		if (event.code === "Tab" && !event.shiftKey) {
			setFocusedOnResults(true);
		}

		if (event.code === "Tab" && event.shiftKey) {
			hideSearchResults();
		}

		if (event.code === "Enter") {
			event.preventDefault();
		}
	};

	const handleWindowKeydown = (event) => {
		if (event.code === "Escape") {
			hideSearchResults();
		}
	};

	const onClearSearch = () => {
		if (!isMobile) {
			hideSearchResults();
			setSearchString("");
			unlockScroll();
		} else {
			setSearchString("");
			setNews(null);
		}
	};

	const handleClickOutsideComponent = React.useCallback((event) => {
		// handling click on ".header-menu-btn" when scroll is locked
		if (event.target.classList.contains("header-menu-btn")) {
			setResultVisible(false);
			setFocusedOnResults(false);
			return;
		}
		if (event.target.closest(".top-header__search") !== topHeaderSearchRef.current && !isMobile) {
			hideSearchResults();
		}
	}, []);

	const toggleSearchMobilePopup = () => {
		setResultVisible((state) => !state);
		setFocusedOnResults((state) => !state);
		toggleScrollLock();
	};

	return (
		<div className="top-header__search" ref={topHeaderSearchRef}>
			<div className="top-header__search-wrapper">
				{isMobile ? (
					<CSSTransition
						in={isResultVisible}
						timeout={500}
						mountOnEnter
						unmountOnExit
						classNames="search-mobile-transition"
					>
						<div className="search-mobile">
							<FocusTrap open={isResultVisible}>
								<div className="search-mobile__inner" tabIndex={-1}>
									<div className="search-mobile__header">
										<button
											className="search-mobile__btn-close"
											aria-label="close search popup"
											onClick={toggleSearchMobilePopup}
										>
											<svg viewBox="0 0 512 512" xmlns="http://www.w3.org/2000/svg">
												<path
													d="M437.5,386.6L306.9,256l130.6-130.6c14.1-14.1,14.1-36.8,0-50.9c-14.1-14.1-36.8-14.1-50.9,0L256,205.1L125.4,74.5  c-14.1-14.1-36.8-14.1-50.9,0c-14.1,14.1-14.1,36.8,0,50.9L205.1,256L74.5,386.6c-14.1,14.1-14.1,36.8,0,50.9  c14.1,14.1,36.8,14.1,50.9,0L256,306.9l130.6,130.6c14.1,14.1,36.8,14.1,50.9,0C451.5,423.4,451.5,400.6,437.5,386.6z"
													fill="currentColor"
												/>
											</svg>
										</button>
									</div>
									<form action="#" className="search-mobile__form" data-focused={isFormFocused}>
										<label className="search-mobile__label" aria-label={t("search-placeholder")}>
											<input
												className="search-mobile__input"
												type="text"
												placeholder={t("search-placeholder")}
												aria-label="input for a search request"
												value={searchString}
												onChange={handleInputChange}
												onKeyDown={handleInputKeydown}
												onFocus={toggleFormFocus}
												onBlur={toggleFormFocus}
											/>
										</label>
										{searchString && (
											<button
												className="search-mobile__btn-clear"
												onClick={onClearSearch}
												aria-label="to clear search field"
											>
												<svg viewBox="0 0 512 512" xmlns="http://www.w3.org/2000/svg">
													<path
														d="M437.5,386.6L306.9,256l130.6-130.6c14.1-14.1,14.1-36.8,0-50.9c-14.1-14.1-36.8-14.1-50.9,0L256,205.1L125.4,74.5  c-14.1-14.1-36.8-14.1-50.9,0c-14.1,14.1-14.1,36.8,0,50.9L205.1,256L74.5,386.6c-14.1,14.1-14.1,36.8,0,50.9  c14.1,14.1,36.8,14.1,50.9,0L256,306.9l130.6,130.6c14.1,14.1,36.8,14.1,50.9,0C451.5,423.4,451.5,400.6,437.5,386.6z"
														fill="currentColor"
													/>
												</svg>
											</button>
										)}
									</form>
									<div className="search-mobile__results">
										{searchStringDebounced && news && news.length ? (
											<nav className="search-results">
												<ul className="search-results__list">
													{news.map(({ id, dateTime, ...props }) => (
														<li key={id} className="search-results__item">
															<Link
																to="/anypage"
																className="search-results__link"
																onClick={hideSearchResults}
															>
																<div className="search-result">
																	<time
																		className="search-result__time"
																		dateTime={dateTime}
																	>
																		{getFormattedTime(
																			dateTime,
																			language,
																			"dateTimeShort"
																		)}
																	</time>
																	<p className="search-result__title">
																		{props[`title_${language}`]}
																	</p>
																</div>
															</Link>
														</li>
													))}
												</ul>
											</nav>
										) : null}
										{searchStringDebounced.length > 2 && news && news.length === 0 && (
											<p>{t("search__news-not-found")}</p>
										)}
									</div>
								</div>
							</FocusTrap>
						</div>
					</CSSTransition>
				) : (
					<>
						<form action="#" className="top-header__search-form" data-focused={isFormFocused}>
							<label className="top-header__search-label" aria-label={t("search-placeholder")}>
								<input
									className="top-header__search-input"
									type="text"
									placeholder={t("search-placeholder")}
									value={searchString}
									onChange={handleInputChange}
									onKeyDown={handleInputKeydown}
									onFocus={toggleFormFocus}
									onBlur={toggleFormFocus}
								/>
							</label>
							{searchString && (
								<button
									className="top-header__search-clear"
									onClick={onClearSearch}
									aria-label="to clear search field"
								>
									<svg viewBox="0 0 512 512" xmlns="http://www.w3.org/2000/svg">
										<path
											d="M437.5,386.6L306.9,256l130.6-130.6c14.1-14.1,14.1-36.8,0-50.9c-14.1-14.1-36.8-14.1-50.9,0L256,205.1L125.4,74.5  c-14.1-14.1-36.8-14.1-50.9,0c-14.1,14.1-14.1,36.8,0,50.9L205.1,256L74.5,386.6c-14.1,14.1-14.1,36.8,0,50.9  c14.1,14.1,36.8,14.1,50.9,0L256,306.9l130.6,130.6c14.1,14.1,36.8,14.1,50.9,0C451.5,423.4,451.5,400.6,437.5,386.6z"
											fill="currentColor"
										/>
									</svg>
								</button>
							)}
						</form>
						<CSSTransition
							in={isResultVisible}
							timeout={500}
							mountOnEnter
							unmountOnExit
							classNames="top-header-search"
						>
							<div className="top-header__search-results">
								{searchStringDebounced && news && news.length ? (
									<nav className="search-results" ref={searchResultsRef}>
										<FocusTrap open={isFocusedOnResults}>
											<ul className="search-results__list" tabIndex={-1}>
												{news.map(({ id, dateTime, ...props }) => (
													<li key={id} className="search-results__item">
														<Link
															to="/anypage"
															className="search-results__link"
															onClick={hideSearchResults}
														>
															<div className="search-result">
																<time
																	className="search-result__time"
																	dateTime={dateTime}
																>
																	{getFormattedTime(
																		dateTime,
																		language,
																		"dateTimeShort"
																	)}
																</time>
																<p className="search-result__title">
																	{props[`title_${language}`]}
																</p>
															</div>
														</Link>
													</li>
												))}
											</ul>
										</FocusTrap>
									</nav>
								) : null}
								{searchStringDebounced.length > 2 && news && news.length === 0 && (
									<p>{t("search__news-not-found")}</p>
								)}
								{searchStringDebounced.length < 3 && <p>{t("search__too-short-request")}</p>}
							</div>
						</CSSTransition>
					</>
				)}
			</div>
			<button className="top-header__search-btn" onClick={toggleSearchMobilePopup} aria-label="open search popup">
				<svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
					<path
						d="M15.7075 14.2968L12.6108 11.1987C14.9279 8.10222 14.2961 3.71356 11.1996 1.39641C8.10308 -0.920736 3.71441 -0.288925 1.39727 2.80759C-0.919881 5.90411 -0.288071 10.2928 2.80845 12.6099C5.29582 14.4712 8.71218 14.4712 11.1996 12.6099L14.2977 15.708C14.687 16.0973 15.3182 16.0973 15.7075 15.708C16.0968 15.3187 16.0968 14.6874 15.7075 14.2982L15.7075 14.2968ZM7.02984 12.012C4.2778 12.012 2.04686 9.78102 2.04686 7.02898C2.04686 4.27695 4.2778 2.04601 7.02984 2.04601C9.78187 2.04601 12.0128 4.27695 12.0128 7.02898C12.0099 9.77977 9.78066 12.009 7.02984 12.012Z"
						fill="#2D2D2D"
					/>
				</svg>
			</button>
		</div>
	);
};

export default TopHeaderSearch;
