import React from "react";
import classNames from "classnames";
import { Link } from "react-router-dom";

import { useLocale } from "../../hooks";
import { getFormattedTime } from "../../services/formatDateTime";

import "./itemArticle.scss";

const ItemArticle = ({ className, title, titles, imageSrc, imageAlt, url, dateTime }) => {
	const { language } = useLocale();
	const itemArticleClassnames = classNames({
		[className]: className,
		"item-article": true,
	});

	return (
		<li className={itemArticleClassnames}>
			<Link className="item-article__image" to="/anypage" aria-label="   ">
				<img src={imageSrc} alt={imageAlt} />
			</Link>

			<Link to="/anypage" className="item-article__title" aria-label="   ">
				<time className="item-article__time" dateTime={dateTime}>
					{getFormattedTime(dateTime, language, "time")}
				</time>
				{titles ? titles[language] : title}
			</Link>
		</li>
	);
};

export default ItemArticle;
