const SectionVideos = () => {
	const { t } = useLocale();
	return (
		<section className="section-three">
			<div className="container">
				<div className="section-three__inner">
					<div className="section-three__header">
						<h2 className="section-three__title title">{t("section-videos__title")}</h2>
						<MoreButton className="section-three__more" label={t("section-videos__more-videos")} url="#" />
						<Link to="/anypage" className="section-three__more-videos">
							{t("section-videos__more-videos")}
						</Link>
					</div>
					<div className="section-three__content">
						{videos && videos.length && (
							<ul className="section-three__list">
								{videos.map((video, index) => (
									<ItemVideo
										key={video.id}
										{...video}
										className="section-three__item"
										isLarge={index === 0}
									/>
								))}
							</ul>
						)}
						<LoadMoreButton
							className={"section-three__button"}
							title={t("btn-more")}
							withoutIcon
							url={"#"}
						/>
					</div>
				</div>
			</div>
		</section>
	);
};

export default SectionVideos;
